import React, { PureComponent } from 'react';
import { propTypes } from 'react-bootstrap/esm/Image';
import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';


const dateFormatter = number => {
  // return moment(date).unix();
  return number+"$";
};

export default class Example extends PureComponent {

  static demoUrl = 'https://codesandbox.io/s/simple-bar-chart-tpz8r';

  render() {
    return (
      <ResponsiveContainer width="100%" height="100%"  margin={{ top: 0, left: 0, right: 0, bottom: 0 }}>
        <BarChart
          width="100%"
          height="100%"
          data={ [
            {
              name: '',
              pv: this.props.data,
              
            }
           
          ]}
          barSize={25}
          
        >
          <XAxis dataKey="name" fontSize="10px" />
          <YAxis fontSize="15px" tickFormatter={dateFormatter} fontColor="#7C9CBF"/>
          <Tooltip />
          
          <Bar dataKey="pv" fill="#5C4899" />
         
        </BarChart>
      </ResponsiveContainer>
    );
  }
}
