const getPredictionContract = async (_predictionIndex = null) => {
    const pid = getPid(_predictionIndex);
    if(predictionContracts[pid])
        return predictionContracts[pid];

    const address = await factoryContract.methods.predictions(pid).call();
    const contract = new web3.eth.Contract(predictionABI, address);
    predictionAddresses[pid] = address;
    predictionContracts[pid] = contract;

    return contract;
}

const getPid = (_predictionIndex) => {
    return _predictionIndex!=null ? _predictionIndex : predictionIndex;
}

const getPredictionAddress = async (_predictionIndex) => {
    if(predictionAddresses[_predictionIndex])
        return predictionAddresses[_predictionIndex];

    const address = await factoryContract.methods.predictions(_predictionIndex).call();
    predictionAddresses[_predictionIndex] = address;
    return address;
}

