export const getRoundData = async (roundNo, _predictionIndex = null) => {
    const predictionContract = await getPredictionContract(_predictionIndex);
    const result = await predictionContract.methods.rounds(roundNo).call();
    const decimal = oracleAddresses[oracleAddress].decimal;
    result.totalAmount = new BigNumber(result.bearAmount).plus(result.bullAmount);
    result.lockPrice = (new BigNumber(result.lockPrice).dividedBy(BIG_TEN.pow(decimal))).toString();
    result.closePrice = (new BigNumber(result.closePrice).dividedBy(BIG_TEN.pow(decimal))).toString();
    result.payoutUp = 0;
    result.payoutDown = 0;
    if(result.bearAmount > 0)
        result.payoutDown = (new BigNumber(result.totalAmount).dividedBy(result.bearAmount)).toFixed(3);
    if(result.bullAmount > 0)
        result.payoutUp = (new BigNumber(result.totalAmount).dividedBy(result.bullAmount)).toFixed(3);
    
    result.totalAmount = (new BigNumber(result.totalAmount).dividedBy(BIG_TEN.pow(18))).toString();

    return result;
}

