const getOracle = async (_predictionIndex = null) => {
    const pid = getPid(_predictionIndex);

    if(oracleCache[pid])
        return oracleCache[pid];

    const oracle = await factoryContract.methods.getOracleInfo(pid).call();
    oracleCache[pid] = oracle;
    return oracle;
}

export const getCurrentPrice = async (_predictionIndex = null) => {
    const pid = getPid(_predictionIndex);
    const oracle = await getOracle(pid);

    const geckoAPIid = oracleAddresses[oracle].geckoAPIid;
    const geckoAPIcurrency = oracleAddresses[oracle].geckoAPIcurrency;
    const priceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${geckoAPIid}&vs_currencies=${geckoAPIcurrency}`);
    const priceData = await priceRes.json();
    return priceData[geckoAPIid][geckoAPIcurrency];
}

//checks if the game is paused or have a problem
//need testing
export const hasStopped = async (_predictionIndex = null) => {

    if(process.env.REACT_APP_PAUSED == "true") return true;

    const predictionContract = await getPredictionContract(_predictionIndex);
    const paused = await predictionContract.methods.paused().call();
    if(paused) return true;

    const currentRound = await getCurrentRoundNo();
    const roundData = await getRoundData(currentRound);
    const currentBlock = await web3.eth.getBlock("latest");
    const interval = await getPredictionIntervalSeconds(_predictionIndex);
    const timeDiff = roundData.closeTimestamp - currentBlock.timestamp;
    if (timeDiff < -interval) return true;

    const genesisCheck = await predictionContract.methods.genesisStartOnce().call();

    return !genesisCheck;
}


