export const subscribePredictionEvents = async (callback) => {
    await unsubscribePredictionEvents();
    const predictionAddress = await getPredictionAddress(predictionIndex);
    
    if(process.env.REACT_APP_TEST == "true")
        console.log('subscribing to events with prediction address: ',predictionAddress);

    //using start round instead of PredictionsRewardsCalculated
    //as they both happen at the same time
    const query = new Moralis.Query("PredictionsStartRound");
    query.equalTo("address", predictionAddress.toLowerCase());
    moralisSubscriptionCalc = await query.subscribe();
    moralisSubscriptionCalc.on("create", function(data) {
        if(process.env.REACT_APP_TEST == "true")
            console.log("Moralis Event - Rewards Calculated: ", data);
        const payload = {};
        if(callback) callback("REWARDSCALCULATED", payload);
    });

    const queryPaused = new Moralis.Query("PredictionsPause");
    queryPaused.equalTo("address", predictionAddress.toLowerCase());
    moralisSubscriptionPause = await queryPaused.subscribe();
    moralisSubscriptionPause.on("create", function(data) {
        if(process.env.REACT_APP_TEST == "true")
            console.log("Moralis Event - Paused: ", data);
        const payload = {};
        if(callback) callback("PAUSED", payload);
    });
}

export const unsubscribePredictionEvents = async () => {
    if(moralisSubscriptionCalc) moralisSubscriptionCalc.unsubscribe();
    if(moralisSubscriptionPause) moralisSubscriptionPause.unsubscribe();
}

