export const getVolume24h = async (_groupName) => {
    groupData = await loadGroupData();
    const pid = groupData[_groupName].ids[0];
    const strIds = groupData[_groupName].ids.join();
    const apiURL = apiBase + "amountLast24" + apiId + `&ids=${strIds}`;
    const data = await fetch(apiURL);
    const datajson = await data.json();
    const amount = new BigNumber(datajson.result).dividedBy(BIG_TEN.pow(18));
    const volume = parseFloat(amount);

    const bettingTokenAddress = await getPredictionBettingTokenAddress(pid);
    const tokenName = tokenContracts[bettingTokenAddress].symbol;
    const geckoAPIid = tokenContracts[bettingTokenAddress].geckoid;
    const priceRes = await fetch(`https://api.coingecko.com/api/v3/simple/price?ids=${geckoAPIid}&vs_currencies=usd`);
    const priceData = await priceRes.json();
    const tokenPrice = priceData[geckoAPIid].usd;

    const totalUSD = (new BigNumber(amount).multipliedBy(tokenPrice)).toFixed(3);

    const result = {tokenName, volume, tokenPrice, totalUSD};
    return result;
}

export const getPriceChangePercentage = async (_groupName) => {
    groupData = await loadGroupData();
    const pid = groupData[_groupName].ids[0];
    const oracle = await getOracle(pid);
    const geckoAPIid = oracleAddresses[oracle].geckoAPIid;

    const priceRes = await fetch(`https://api.coingecko.com/api/v3/coins/${geckoAPIid}/market_chart?vs_currency=usd&days=1&interval=daily`);
    const priceData = await priceRes.json();

    if(!priceData.hasOwnProperty('prices') || priceData['prices'].length < 2){
        return 0;
    }
    
    const currentPrice = priceData['prices'][0][1];
    const oldPrice = priceData['prices'][1][1];

    return (100 * (parseFloat(currentPrice)- parseFloat(oldPrice)) / parseFloat(oldPrice)).toFixed(2);
}

