export const getGroupData = async (tokenName) => {
    groupData = await loadGroupData();
    const ids = groupData[tokenName]['ids'];

    if(ids.length === 1) return ids;

    let orderedIds = [];

    for(let i=0; i < ids.length; i++){
        let lockTimestamp = await getPredictionCurrentRoundLockTimestamp(ids[i]);
        if(lockTimestamp < Date.now()/1000) lockTimestamp = parseInt(lockTimestamp) + 10000000;
            
        orderedIds.push({lockTimestamp, id:ids[i]});
    }

    orderedIds.sort((a,b) => a.lockTimestamp - b.lockTimestamp);
    const result = orderedIds.map(i => i.id);
    return result;
}

