export var groupData = null;

export const loadGroupData = async () => {
    if(groupData === null){
        let api = '';
        if(process.env.REACT_APP_TEST == "true"){
            api = "https://5th7hmn6ht0q.moralisweb3.com:2053/server/functions/getGroupData?_ApplicationId=TlXh5355T7kgd5HrLSCuJ4bZmxxnmuUTCXxBRWhk";
        }
        else{
            api = "https://91hd9xaj0ufh.moralishost.com:2053/server/functions/getGroupData?_ApplicationId=9oc5tZ3PLKUK9QK0zbVMaujdMZtrXtT35E8BwD1E";
        }
        const response = await fetch(api);
        let data = await response.json();
        groupData = data.result;
        return groupData;
    }
    else
        return groupData;
}

